import React, { Component } from 'react';
import Image from './Image';
import Video from './Video';
import TextReview from './TextReview';

export default class Media extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: '100',
      imgWidth: '100',
      error: '',
      imgUrl: props.url
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { height, width } = props;

    if (height && height !== "" && width && width !== "") {
      this.setState({
        imgHeight: (parseInt(height) * 100) / parseInt(width),
        imgWidth: (parseInt(width) * 100) / parseInt(height)
      })
    } else {
      // const image = await loadImage(imgUrl);
      // this.setState({
      //     height: (image.height * 100) / image.width,
      //     width: (image.width * 100) / image.height
      // })
    }
  }
  onErrorImageUpdate = event => event.target.src = "https://cloud.taggbox.com/widget/themes/media/images/no-image.svg"

  render() {
    const { height, width, alt, url } = this.props
    const { imgUrl } = this.state

    const imageStyle = {
      paddingBottom: '100%', width: '100%'
    };

    return  <>
                {/* <TextReview height={height} width={width} url={url} alt={alt} /> */}
                <Image height={height} width={width} url={url} alt={alt} />
                {/* <Video height={height} width={width} url={url} alt={alt} /> */}
            </>
  }
}
